import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ToastController } from "@ionic/angular";
import { UserService } from "src/app/services/user.service";
import { FormGroup, FormControl, Validators } from "@angular/forms";

@Component({
  selector: "app-atualiza-peso",
  templateUrl: "./atualiza-peso.page.html",
  styleUrls: ["./atualiza-peso.page.scss"],
})
export class AtualizaPesoPage implements OnInit {
  form: FormGroup;

  constructor(
    private router: Router,
    private userSrv: UserService,
    private toastCtrl: ToastController
  ) {}

  ngOnInit() {
    this.initializeForm();
  }

  initializeForm() {
    this.form = new FormGroup({
      peso: new FormControl(null, [Validators.required]),
    });
  }

  async submitWeight() {
    const weight = this.form.value;
    if (weight.peso === null) {
      this.showToast("Insira o seu peso atualizado");
      return;
    }

    this.userSrv.insertWeight(weight).subscribe(
      async () => {
        await this.showToast("Peso inserido com sucesso");
        await this.router.navigate(["tabs/tab4"]);
      },
      async (error) => {
        await this.showToast(error);
      }
    );
  }

  onBack() {
    this.router.navigate(["profile"]);
  }

  async showToast(message: string) {
    const toast = await this.toastCtrl.create({
      message,
      duration: 1000,
    });

    await toast.present();
  }
}
